/**
 * Utility classes
 * ----------------------------------------------------------------
 */

/**
 * Floats
 */

.clearfix {
  @include clearfix();
}
.center-block {
  @include center-block();
}
.pull-right {
  float: right !important;
}
.pull-left {
  float: left !important;
}

/**
 * Positioning
 */
.relative {
  position: relative !important;
}
.vertical-align-middle { // use together with `.row`
  display: table;
  table-layout: fixed;

  > div {
    display: table-cell;
    float: none;
    height: 100%;
    vertical-align: middle;
  }
}

/**
 * Toggling content
 */
// Note: Deprecated .hide in favor of .hidden or .sr-only (as appropriate) in v3.0.1
.hide {
  display: none !important;
}
.show {
  display: block !important;
}
.invisible {
  visibility: hidden;
}
// .text-hide {
//   @include text-hide();
// }


/**
 * Only display content to screen readers
 */
// See: http://a11yproject.com/posts/how-to-hide-content/

.sr-only {
  border: 0;
  clip: rect(0,0,0,0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
}

// Use in conjunction with .sr-only to only display content when it's focused.
// Useful for "Skip to main content" links; see http://www.w3.org/TR/2013/NOTE-WCAG20-TECHS-20130905/G1
// Credit: HTML5 Boilerplate

.sr-only-focusable {
  &:active,
  &:focus {
    position: static;
    width: auto;
    height: auto;
    margin: 0;
    overflow: visible;
    clip: auto;
  }
}


/**
 * Hide from screenreaders and browsers
 */
// Credit: HTML5 Boilerplate

.hidden {
  display: none !important;
  visibility: hidden !important;
}


/**
 * For Affix plugin
 */
// -------------------------

.affix {
  position: fixed;
  transform: translate3d(0, 0, 0);
}

/**
 * Divider
 */

.divider {
  display: block;
  height: 10px;
}

.space {
  display: inline;
  padding: 5px;
}

/**
 * Extra margin helpers
 */
// e.g. for use on rows that serve as e.g. bars (.bar extends both classes btw)
.margin-bottom {
  margin-bottom: rem($padding-large-vertical * 1.5);
}
.margin-top {
  margin-top: rem($padding-large-vertical * 1.5);
}
.margin-left {
  margin-left: rem($padding-large-horizontal * 1.5);
}
.margin-right {
  margin-right: rem($padding-large-horizontal * 1.5);
}
.margin-bottom-lg {
  margin-bottom: rem($padding-large-vertical * 3);
}
.margin-top-lg {
  margin-top: rem($padding-large-vertical * 3);
}
.margin-left-lg {
  margin-left: rem($padding-large-horizontal * 3);
}
.margin-right-lg {
  margin-right: rem($padding-large-horizontal * 3);
}
.margin-bottom-sm {
  margin-bottom: rem($padding-large-vertical * .75);
}
.margin-top-sm {
  margin-top: rem($padding-large-vertical * .75);
}
.margin-left-sm {
  margin-left: rem($padding-large-horizontal * .75);
}
.margin-right-sm {
  margin-right: rem($padding-large-horizontal * .75);
}
.no-margin {
  margin: 0 !important;
}


/**
 * Remove borders
 */
.no-border {
  border: none !important;
}
